import axios from "axios";
import React, { useEffect, useState } from "react";
import Layout from "./Layout";

function Slug(props) {
  const [movie, setMovie] = useState(null);
  // const [selected, setSelected] = useState(null);
  useEffect(() => {
    console.log(props)
    axios
      // .get(`https://aha231.herokuapp.com/data/`)
      .get(`https://api-aha.herokuapp.com/movies/${props.match.params.id}`)
      // .then((res) => setMovies(res.data))
      .then((res) => setMovie(res.data))
      .catch((err) => err.message);
  }, []);

  return (
    <div >
      {movie === null ? (
        <Layout style={{marginTop:'200%'}}>Loading...</Layout>
      ) : (
        <Layout>
          <div className="card" style={{ marginLeft: "35%",marginTop:"10%",marginBottom: "-14%",width: '360px',objectFit:'cover'}}>
            {/* <img
              src={selected?.image}
              style={{ height: "300px", width: "300px", padding: "12px" }}
            />
            <h4>{selected?.name}</h4>
            <p>{selected?.info}</p> */}
            <img
              src={movie?.image}
              class="card-img-top" style={{ height: "300px", width: "360px", padding: "12px" }}
            />
            {/* <h4>{movie?.name}</h4>
            <p>{movie?.info}</p> */}
            <div class="card-body">
              <h5 class="card-title" style={{color:"black"}}>{movie?.name}</h5>
              <p class="card-text" style={{color:"black"}}>{movie?.info}</p>
              <a href="#" class="btn btn-primary">Watch Movie</a>
            </div>
          </div>
          
        </Layout>
      )}
    </div>
  );
}

export default Slug;


