import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import "./Movies.css";

import axios from "axios";
const Movies = () => {
  const [photoCollection, setphotoCollection] = useState([]);
  //  okati variable and inkokati function which can be called

  useEffect(() => {
    axios
      // post "get" update delete
      .get("https://api-aha.herokuapp.com/movies")
      //   ochina data ni res nunchi state lo load chesatham (photoCollection)
      .then((res) => setphotoCollection(res.data))
      .catch((err) => console.log(err.message));
  }, []); 

  return (
    <React.Fragment>
      <div>
        <section>
          <div className="main" id="movies">
            <div className="trendingMovies movies">
              <p className="heading">Trending Movies</p>
              <div className="cards">
              {photoCollection.filter((y) => y.category_id === 2).map((x) => (
                  <div key={x.id}>
                    <Link to={`/movies/${x._id}`}>
                      <img
                        src={x.image}
                        alt={x.title}
                        style={{
                          height: "160px",
                          width: "258px",
                          padding: "12px",
                         // objectFit:"cover"
                         borderRadius:"8%",
                         transform:'1.1',
                        }}
                      />
                    </Link>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </section>
      </div>
    </React.Fragment>
  );
};

export default Movies;
