import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import "./Movies.css";
import axios from "axios";

const Coming = () => {
  const [photoCollection, setphotoCollection] = useState([]);
  useEffect(() => {
    window.scrollTo(0,0)
    axios
      .get("https://api-aha.herokuapp.com/movies")
      .then((res) => setphotoCollection(res.data))
      .catch((err) => console.log(err.message));
  }, []);

  return (
    <React.Fragment>
      <div className="margin">
        <section style={{marginTop:"168px"}}>
          <div className="main" id="coming">
            <div className="trendingMovies movies">
              <p className="heading">Coming Movies</p>
              <div className="cards">
                {photoCollection.map((x) => (
                  <div key={x.id}>
                    <Link to={`/movies/${x._id}`}>
                      <img
                        src={x.image}
                        alt={x.title}
                        style={{
                          height: "158px",
                          width: "258px",
                          padding: "12px",
                          objectFit: "cover",
                        }}
                      />
                    </Link>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </section>
      </div>
    </React.Fragment>
  );
};

export default Coming;
