import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import './Carousel.css'
import {ArrowBackIos,ArrowForwardIos} from "@material-ui/icons"

const PreviousBtn=(props)=>{
    // console.log(props);
    const{className,onClick} = props;
    return(
        <div className={className} onClick={onClick}>
            <ArrowBackIos style={{color: "orange"}}/>
        </div>
    )
}
const NextBtn=(props)=>{
    // console.log(props);
    const{className,onClick} = props
    return(
        <div className={className} onClick={onClick}>
        <ArrowForwardIos style={{color:"orange"}}/>
        </div>
    )
}
const Carousel =() =>{
    return(
        <div style={{margin:'30px'}}>
            <Slider
            autoplay
            autoplaySpeed={3000}
            prevArrow={<PreviousBtn/>}
            nextArrow={<NextBtn/>}
            lazyLoad= {'ondemand'}
            slidesToScroll={1}
            initialSlide={1} 
            centerMode= {true}
            arrows= {true}
            adaptiveHeight= {true} 
            >
                <div>
                    <img src="https://i.ibb.co/rFBVCBk/manchi-rojulochayi.jpg" style={{width:"100%",height:"400px" }}/>
                </div>
                <div>
                    <img src="https://i.ibb.co/r0b9Hvq/most-elegible-bachelor.jpg" style={{width:"100%",height:"400px" }}/>
                </div>
                <div>
                    <img src="https://i.ibb.co/k8MnZqN/pushpakav.jpg" style={{width:"100%",height:"400px" }} />
                </div>
                <div>
                    <img src="https://i.ibb.co/wLKN5Rb/unstopable.jpg" style={{width:"100%",height:"400px" }} />
                </div>
            </Slider>
        </div>
    );
}

export default Carousel;